<template>
  <div class="m-publish-box">
    <!--  -->
    <pubheader name="" :localDraft="false">
      <slot name="header"></slot>
    </pubheader>

    <h1 class="m-publish-item-header"></h1>

    <el-form label-position="left" label-width="80px" class="m-publish-item">
      <!--  -->
      <el-form-item label="">
        <el-input v-model="plan.title" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-radio v-model="plan.public" :label="0"></el-radio>
        <el-radio v-model="plan.public" :label="1"></el-radio>
      </el-form-item>
      <!--  -->
      <el-form-item label="">
        <el-input type="textarea" :rows="3" v-model="plan.description" placeholder=""></el-input>
      </el-form-item>
      <!--  -->
      <el-form-item label="">
        <el-radio-group v-model="plan.type" size="medium">
          <el-radio-button label="1"></el-radio-button>
          <el-radio-button label="2"></el-radio-button>
        </el-radio-group>
      </el-form-item>
      <!--  -->
      <el-form-item label="">
        <el-row style="line-height:initial;">
          <el-col :xs="24" :sm="5" class="c-item-search">
            <el-input placeholder="" prefix-icon="el-icon-search" v-model="keyword"></el-input>
            <draggable class="m-search-items" tag="ul" v-if="items && items.length" :list="items" :sort="false"
                       :group="{ name: 'draggable-item', pull: 'clone', put: false }" :move="move_handle"
                       ghost-class="ghost">
              <li v-for="(item,key) in items" :key="key" class="m-search-item">
                <jx3-item-simple :item="item"/>
              </li>
            </draggable>
            <div v-if="items && !items.length" class="m-no-item">
              <i class="el-icon-chat-line-round"></i>
              <span>&nbsp;</span>
            </div>
          </el-col>

          <el-col :xs="24" :sm="19" class="c-plan-relation c-normal-relation" v-if="plan.type==1">
            <div class="m-positions">
              <div class="m-position" v-for="(position,key) in normal_relation" :key="key">
                <el-input class="u-title" v-model='position.title' placeholder=""></el-input>
                <div class="c-selected-items">
                  <draggable class="m-selected-items" tag="ul" :list="position.data" group="draggable-item"
                             :class="{empty:!position.data||!position.data.length}" ghost-class="ghost">
                    <li v-for="(item,k) in position.data" :key="k" class="m-selected-item">
                      <jx3-item-simple :item="item"/>
                      <i class="u-el-icon el-icon-close" @click="position.data.splice(k,1)"></i>
                    </li>
                  </draggable>
                  <span v-if="!position.data.length" class="u-tip"></span>
                </div>
                <i class="u-el-icon el-icon-close" @click="normal_relation.splice(key,1)"></i>
              </div>
            </div>
            <el-row :gutter="15">
              <el-col :xs="24" :sm="6">
                <div class="m-position-add" @click="add_normal_relation">+</div>
              </el-col>
            </el-row>
          </el-col>

          <el-col :xs="24" :sm="19" class="c-plan-relation" v-if="plan.type==2">
            <el-row gutter="15" class="m-positions">
              <el-col :xs="24" :sm="6" v-for="(_positions,index) in positions" :key="index">
                <div class="m-position" v-for="(position,key) in _positions" :key="key">
                  <h3 class="u-title" v-text="position.label"></h3>
                  <div class="c-selected-items">
                    <draggable class="m-selected-items m-selected-equips" tag="ul" :list="equip_relation[key]"
                               group="draggable-item" :move="move_handle"
                               :data-AucGenre="position.AucGenre" :data-AucSubType="position.AucSubType"
                               :class="{empty:!equip_relation[key]||!equip_relation[key].length}" ghost-class="ghost">
                      <li v-for="(item,k) in equip_relation[key]" :key="k" class="m-selected-item">
                        <jx3-item-simple :item="item"/>
                        <i class="u-el-icon el-icon-close" @click="equip_relation[key].splice(k,1)"></i>
                        <span v-if="k==0" class="u-main"></span>
                      </li>
                    </draggable>
                    <span v-if="!equip_relation[key].length" class="u-tip"></span>
                  </div>
                </div>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button class="u-publish" icon="el-icon-s-promotion" type="success" @click="submit"
                   :disabled="$store.state.processing">
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import pubheader from "@/components/publish/pubheader.vue";
  import ItemSimple from '@jx3box/jx3box-editor/src/ItemSimple';
  import draggable from 'vuedraggable';

  // 
  import {search_items} from "../service/item";
  import {get_item_plan, save_item_plan} from "../service/item_plan";

  const qs = require("qs");
  const lodash = require("lodash");
  const {__Root} = require("@jx3box/jx3box-common/js/jx3box.json");

  export default {
    name: "item",
    props: [],
    data: function () {
      return {
        keyword: '',
        items: null,
        normal_relation: [{title: '', data: []}],
        equip_relation: {
          '1': [],
          '2': [],
          '3_1': [],
          '3_2': [],
          '3_3': [],
          '3_4': [],
          '3_5': [],
          '3_6': [],
          '4_1': [],
          '4_3': [],
          '4_2_1': [],
          '4_2_2': [],
        },
        plan: {
          id: '',
          title: '',
          public: 0,
          type: 1,
          relation: null,
          description: '',
        },
        positions: [
          {
            '1': {label: '', AucGenre: 1},
            '2': {label: '', AucGenre: 2},
          },
          {
            '3_2': {label: '', AucGenre: 3, AucSubType: 2},
            '3_1': {label: '', AucGenre: 3, AucSubType: 1},
            '3_3': {label: '', AucGenre: 3, AucSubType: 3},
          },
          {
            '3_6': {label: '', AucGenre: 3, AucSubType: 6},
            '3_4': {label: '', AucGenre: 3, AucSubType: 4},
            '3_5': {label: '', AucGenre: 3, AucSubType: 5},
          },
          {
            '4_1': {label: '', AucGenre: 4, AucSubType: 1},
            '4_3': {label: '', AucGenre: 4, AucSubType: 3},
            '4_2_1': {label: '', AucGenre: 4, AucSubType: 2, index: 1},
            '4_2_2': {label: '', AucGenre: 4, AucSubType: 2, index: 2},
          },
        ],
      };
    },
    mounted() {
      if (this.$route.params.plan_id) {
        console.log(4124124, this.$route.params.plan_id);
        get_item_plan(this.$route.params.plan_id).then(
          (data) => {
            data = data.data;
            if (data.code === 200) {
              this.plan = data.data.plan;
              if (this.plan) {
                if (this.plan.type == 1) {
                  this.normal_relation = this.plan.relation_items;
                } else if (this.plan.type == 2) {
                  this.equip_relation = this.plan.relation_items;
                } else {
                  this.$message.error('');
                }
              } else {
                this.$message.error(data.message);
                this.$router.push({name: 'item_plan'})
              }
            } else {
              this.$message.error('');
            }
          }
        )
      }
    },
    methods: {
      add_normal_relation() {
        this.normal_relation.push({title: '', data: []});
      },
      move_handle(e) {
        if (!e.to.classList.contains('m-selected-items')) return false;
        if (!e.to.classList.contains('m-selected-equips')) return true;
        let AucGenre = e.to.getAttribute('data-AucGenre');
        let AucSubType = e.to.getAttribute('data-AucSubType');
        let result = e.draggedContext.element.AucGenre == AucGenre;
        if (AucSubType !== null) result = result && e.draggedContext.element.AucSubType == AucSubType;
        return result;
      },
      search() {
        search_items(this.keyword, 15, ['id', 'UiID', 'Name', 'IconID', 'Quality', 'AucGenre', 'AucSubType'], this.plan.type == 2).then(
          (data) => {
            data = data.data;
            this.items = data.code === 200 ? data.data.data : [];
          },
          () => {
            this.options.items = false;
          }
        );
      },
      submit() {
        if (this.plan.type == 1) {
          // PlanPlan ID
          let relation = JSON.parse(JSON.stringify(this.normal_relation));
          for (let key in relation) {
            for (let k in relation[key].data) {
              relation[key].data[k] = relation[key].data[k].id
            }
          }
          this.plan.relation = JSON.stringify(relation);
        } else if (this.plan.type == 2) {
          // PlanPlan ID
          let relation = JSON.parse(JSON.stringify(this.equip_relation));
          for (let key in relation) {
            for (let k in relation[key]) {
              relation[key][k] = relation[key][k].id
            }
          }
          this.plan.relation = JSON.stringify(relation);
        } else {
          this.$message.error('');
          return;
        }

        this.$store.commit('startProcess');
        save_item_plan(this.plan).then(
          (data) => {
            data = data.data;
            if (data.code === 200) {
              this.$message({
                message: "", type: "success", onClose: () => {
                  location.href = `${__Root}item/#/plan_view/${data.data.id}`;
                }
              });
            } else {
              this.$message({message: `${data.message}`, type: "warning"});
            }
          }
        )
      }
    },
    watch: {
      keyword: {
        immediate: true,
        handler() {
          this.search();
        }
      },
      'plan.type': function () {
        this.search();
      }
    },
    components: {
      draggable,
      pubheader,
      'jx3-item-simple': ItemSimple,
    },
  };
</script>

<style lang="less">
  @import "../assets/css/publish/item_plan.less";
</style>
